.home-new-vi {
    // body class override, development only
    &.cms-editor {
        div.cms-editor-warning {
            display: none;
        }
    }

    .breadcrumbs {
        display: none;
    }

    .navigation--secondary {
        display: none;
    }

    .triangle__block {
        width: $measure--huge5;
    }

    .triangle__block--left {
        margin-bottom: -$measure--huge3;
    }

    .triangle__block--right {
        right: 0;
        position: absolute;
        transform: rotate(180deg);
        margin-top: -$measure--huge5;
    }

    main .navigation--secondary + article {
        grid-column: 1/-1;
    }
}

.home-new-vi {
    main {
        margin: 0;
        padding: 0;
        max-width: inherit;

        p {
            max-width: $max-line-length;
        }
    }
}
